import React, { Component } from 'react';
import Percentage from './components/Percentage';
import MovieRight from '../images/movie-right.svg';

class DetailMm extends Component{
	render(){
		return <div className="animated fadeIn">
			<section>
				<h2>評分標準</h2>
				<h3>初賽與決賽</h3>
				<Percentage value="20" title="作品原創性" />
				<Percentage value="30" title="主題符合性" />
				<Percentage value="30" title="整體創意" />
				<Percentage value="20" title="創作說明" />
			</section>
			<section>
				<h2>賽制規則</h2>
				<p>本競賽分為初賽與決賽，初賽由各隊根據競賽主題撰寫微電影腳本提案企劃書，決賽須依據企劃書內容拍攝微電影作品。</p>
				<h3>初賽</h3>
				<p>
					<ol>
						<li>微電影主題包括二子題。一為企業經營的內永續：<strong>獲利與誠信的兩難</strong>。二為企業經營的外永續：<strong>企業獲利與全球永續目標的共好</strong>。參賽團隊可以就此二子題為構思方向，找出一則自身領域的實際案例來撰寫微電影提案企劃書。</li>
						<li>各隊之微電影提案企劃書須於 2018 年 10 月 29 日（一）17:00 前上傳至指定雲端空間。</li>
						<li>所有參賽隊伍依照審核成績，擇最優9隊進入決賽。</li>
					</ol>
				</p>
				<h3>決賽</h3>
				<p>
					<ol>
						<li>入圍決賽之隊伍需依微電影提案企劃書內容實際拍攝完成 10~15 分鐘（含片頭及片尾）之微電影作品。決賽當天以現場播放影片方式進行，並統一抽籤決定播放順序，抽籤未到者視同棄權。入圍隊伍依抽籤順序依序播放各隊伍影片，影片結束後由現場評審進行 5 分鐘答詢。</li>
						<li>決賽隊伍需於 2018 年 12 月 7 日（五）17:00 前上傳影片到指定雲端空間，未於規定時間前上傳者，視同棄權。</li>
						<li>參加決賽隊伍的成員必須與初賽隊員相同，如因不可抗力之因素須更換成員，則以 1 名為限，並於決賽前事先告知主辦單位。</li>
						<li>影片類型、拍攝工具不限，影片建議可加旁白、字幕、製作團隊及感謝字幕等內容，但勿出現學校系所全名。</li>
						<li>各參賽團隊請繳交作品光碟一份及授權協進會使用素材進行競賽相關宣傳。</li>
						<li>影片格式以 wmv、mp4、avi 為主，解析度 1920 x 1080 （以上）為主，低階格式概不受理；所有參賽作品請自行保留母帶，一經遞交，概不發還。</li>
						<li>評審會議決定第一名、第二名、第三名與 6 隊優勝隊伍，並依決賽名次頒予獎金和獎牌（或獎狀）。</li>
					</ol>
				</p>
			</section>
			<section className="prize">
				<h2>競賽獎勵</h2>
				<h3>學生獎勵</h3>
				<ul>
				<li>第一名：隊伍獲得獎牌乙座及獎金新台幣 20,000 元</li>
				<li>第二名：隊伍獲得獎牌乙座及獎金新台幣 10,000 元</li>
				<li>第三名：隊伍獲得獎牌乙座及獎金新台幣 8,000 元</li>
				<li>優勝六名：各隊伍獲得獎狀乙張及獎金新台幣 2,000 元</li>
				</ul>
				<h3>指導老師獎勵</h3>
				<ul>
				<li>第一名：指導老師獲得獎狀乙紙及獎金新台幣 6,000 元</li>
				<li>第二名：指導老師獲得獎狀乙紙及獎金新台幣 3,000 元</li>
				<li>第三名：指導老師獲得獎狀乙紙及獎金新台幣 2,000 元</li>
				<li>優勝六名：各隊伍指導老師分別獲得獎狀乙紙及獎金新台幣 1,000 元</li>
				</ul>
				<img src={MovieRight} alt="" className="bg"/>
			</section>
		</div>
	}
}

export default DetailMm;



