import React, { Component } from 'react';
import './App.css';
import GrandpaSmall from './images/grandpa-small.svg';
import MovieLeft from './images/movie-left.svg';
import moment from 'moment';
import 'moment/locale/zh-tw';
import 'animate.css'
import Main from './ui/Main';
import DetailCa from './ui/DetailCa';
import DetailMm from './ui/DetailMm';

class App extends Component {
	constructor(props){
		super(props);
		this.state = {
			currentSection: 0,
		};
	}

	changeSection(r){
		this.setState({currentSection: r});
	}

	render() {
		moment.locale('zh-tw');
		return (
			<div className="App">
				<header>
					<div className="time">第八屆</div>
					<div className="desc">全國大專院校</div>
					<div className="contest-1 contest">
						<img className="logo logo-grandpa" src={GrandpaSmall} alt="" />
						倫理個案分析
					</div>
					<div className="and">暨</div>
					<div className="contest-2 contest">
						微電影競賽
						<img className="logo animated tada" src={MovieLeft} alt="" />
					</div>
					<div className="signup">報名剩下 {moment('2018-10-03 17:00').locale('zh-tw').fromNow().slice(0, -1)}</div>
					<div className="signup-small">2018-10-03 17:00 截止</div>
					<nav>
						<ul>
							<li onClick={this.changeSection.bind(this, 0)}
								className={this.state.currentSection === 0 ? 'active' : ''}>活動內容</li>
							<li onClick={this.changeSection.bind(this, 1)}
								className={this.state.currentSection === 1 ? 'active' : ''}>倫理個案分析</li>
							<li onClick={this.changeSection.bind(this, 2)}
								className={this.state.currentSection === 2 ? 'active' : ''}>微電影</li>
						</ul>
					</nav>
				</header>
				{this.state.currentSection === 0 ? <Main /> : null}
				{this.state.currentSection === 1 ? <DetailCa /> : null}
				{this.state.currentSection === 2 ? <DetailMm /> : null}
				<footer>
					<div className="copyright">Copyright 2018 全國大專院校倫理個案分析暨微電影競賽</div>
				</footer>
			</div>
		);
	}
}

export default App;



