import React, { Component } from 'react';
import './Percentage.css';

class Percentage extends Component{
	render(){
		return <div className="percentage">
			<div className="circle animated rubberBand">
				{this.props.value}
				<div className="p">%</div>
			</div>
			<div className="title">
				{this.props.title}
			</div>
			{this.props.desc ? <div className="pdesc">{this.props.desc}</div> : null}
		</div>
	}
}

export default Percentage;



