import React, { Component } from 'react';
import Percentage from './components/Percentage';
import GrabndpaBig from '../images/grandpa-big.svg';

class DetailCa extends Component{
	render(){
		return <div className="animated fadeIn">
			<section>
				<h2>評分標準</h2>
				<h3>初賽</h3>
				<Percentage value="30" title="個案議題辨識" desc="針對比賽個案內容，發覺主要問題並加以分析。" />
				<Percentage value="30" title="個案問題回答" desc="針對所發掘之議題提供建議及解決方案。" />
				<Percentage value="25" title="具企業倫理的啟發性" desc="所提之見解隱含可供其他企業參酌的倫理準則。" />
				<Percentage value="15" title="書面報告格式" desc="書面報告的編排是否整齊、有條理、美觀、易讀。" />
				<h3>決賽</h3>
				<Percentage value="30" title="個案議題辨識" desc="針對比賽個案內容發掘主要問題，並提供建議及可行的解決方案。" />
				<Percentage value="35" title="內容架構與組織性	" desc="簡報內容完整、邏輯清晰性、假設與詮釋清楚明白、時間精準掌控，與適度的視覺輔助。" />
				<Percentage value="25" title="問答情形" desc="答覆技巧、反應，以及針對問題進行答覆。" />
				<Percentage value="10" title="團隊表現" desc="團隊的整體形象、默契，及出席情形。" />
			</section>
			<section>
				<h2>賽制規則</h2>
				<p>倫理個案競賽分為初賽與決賽，初賽和決賽分別指定企業倫理個案進行分析與報告撰寫，初賽與決賽之競賽題目不同。</p>
				<h3>初賽</h3>
				<p>
					<ol>
						<li>初賽主題為企業經營的內永續：<strong>獲利與誠信的兩難</strong>。參賽團隊請依此主題為依據，找出一則自身領域的實際案例來撰寫個案分析。</li>
						<li>各隊初賽作品之電子檔須於 2018 年 10 月 29 日（一）17:00 之前上傳至指定雲端空間。</li>
						<li>所有參賽隊伍依照審核成績，擇最優9隊進入決賽。</li>
					</ol>
				</p>
				<h3>決賽</h3>
				<p>
					<ol>
						<li>決賽主題為企業經營的外永續：<strong>企業獲利與全球永續目標的共好</strong>。參賽團隊請依此主題為依據，進行個案描述與分析。</li>
						<li>決賽隊伍需於 2018 年 12 月 7 日（五）17:00 前上傳個案分析電子檔至指定雲端空間。未於規定時間前上傳者，視同棄權。</li>
						<li>決賽隊伍需於 2018 年 12 月 15 日（六）上午報到時繳交簡報檔，並統一抽籤決定簡報順序，抽籤未到者視同棄權。</li>
						<li>參加決賽隊伍的成員必須與初賽隊員相同，如因不可抗力之因素必須更換隊員，則以1名為限，並於決賽前事先告知主辦單位。</li>
						<li>決賽以現場簡報進行，入圍隊伍依抽籤順序進行10分鐘個案簡報，由次一報告團隊與現場評審各提出 1 個問題，進行問題答詢。（範例：第一組報告後，由第二組報告的團隊提問；第二組報告後，由第三組報告的團隊提問；以此類推……，第九組報告後，由第一組報告團隊提問）。</li>
						<li>評審會議決定第一名、第二名、第三名與6隊優勝隊伍，並依決賽名次頒發獎金和獎牌（或獎狀）。</li>
					</ol>
				</p>
			</section>
			<section className="prize">
				<h2>競賽獎勵</h2>
				<h3>學生獎勵</h3>
				<ul>
				<li>第一名：隊伍獲得獎牌乙座及獎金新台幣 20,000 元</li>
				<li>第二名：隊伍獲得獎牌乙座及獎金新台幣 10,000 元</li>
				<li>第三名：隊伍獲得獎牌乙座及獎金新台幣 8,000 元</li>
				<li>優勝六名：各隊伍獲得獎狀乙張及獎金新台幣 2,000 元</li>
				</ul>
				<h3>指導老師獎勵</h3>
				<ul>
				<li>第一名：指導老師獲得獎狀乙紙及獎金新台幣 6,000 元</li>
				<li>第二名：指導老師獲得獎狀乙紙及獎金新台幣 3,000 元</li>
				<li>第三名：指導老師獲得獎狀乙紙及獎金新台幣 2,000 元</li>
				<li>優勝六名：各隊伍指導老師分別獲得獎狀乙紙及獎金新台幣 1,000 元</li>
				</ul>
				<img src={GrabndpaBig} alt="" className="bg"/>
			</section>
		</div>
	}
}

export default DetailCa;



