import React, { Component } from 'react';
import './Sponsor.css';

class Sponsor extends Component{
	render(){
		return <a target="_blank" href={this.props.link} className="sponsor-link">
			<div className="sponsor">
				<img src={this.props.img} alt="" />
				<div className="sponsor-title">{this.props.title}</div>
			</div>
		</a>
	}
}

export default Sponsor;



